events.on('ready', function() {
    $('.botao').on('click', function(){
        $(this).removeClass('animated');
        $(this).addClass('visited');
        if ($('.flip-click.visited').length >= $('.flip-click').length && $('.botao').hasClass('visited')) {
            scorm.saveObject('trilha2', true);
            $('.arrow-right').removeClass('hide');
        }
    });

    $('.flip-click').on('click', function() {
        $(this).addClass('visited');
        $(this).removeClass('animated');

        if ($('.flip-click.visited').length >= $('.flip-click').length && $('.botao').hasClass('visited')) {
            scorm.saveObject('trilha2', true);
            $('.arrow-right').removeClass('hide');
        }
    });
});